<template>
  <div>
    <v-card width="450px" class="mx-auto mt-12">
      <template v-if="register == false">
        <v-card-title class="justify-center">  Login </v-card-title>
        <v-card-text>
          <v-form>
            <v-text-field
              label="Email"
              prepend-icon="mdi-account-circle"
              v-model="user.email"
            />
            <v-text-field
              label="Senha"
              :type="showPassword ? 'text' : 'password'"
              prepend-icon="mdi-lock"
              :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
              @click:append="showPassword = !showPassword"
              v-model="user.password"
            />
          </v-form>
        </v-card-text>
        <v-divider />
        <v-card-actions>
          <v-btn 
            text 
            @click.stop="register = true" 
            color="success"
          >
            Cadastrar
          </v-btn>

          <v-spacer />

          <v-btn 
            text 
            color="info"
            @click="signIn"
          >
            Entrar
          </v-btn>
        </v-card-actions>
      </template>

      <template v-else>
        <v-card-title class="justify-center">  Cadastro </v-card-title>
        <v-card-text>
          <v-form class="px-3">
              <v-text-field 
                label="Nome"
                v-model="newUser.name"
              />
              <v-text-field 
                label="Email"
                v-model="newUser.email"
              />
              <v-text-field 
                label="Contato"
                v-model="newUser.contact"
              />
              <v-text-field 
                label="Senha"
                v-model="newUser.password"
              />
              <v-text-field 
                label="Confirmar Senha"
                v-model="newUser.confirmPassword"
              />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn text @click.stop="register = false" color="primary">Voltar</v-btn>
          <v-spacer />
          <v-btn text @click.stop="" color="success" @click="signUp">Cadastrar-se</v-btn>
        </v-card-actions>
      </template>
    </v-card>
    <ShowMessage
      :type="snackType"
      :text="snackText"
      :visible="snackVisible"
    />
  </div>
</template>

<script>
export default {
  data(){
    return {
      snackVisible: false,
      snackTitle: '',
      snackText: '',
      register: false,
      showPassword: false,
      user: {
        email: '',
        password: ''
      },
      newUser: {
        name: '',
        email: '',
        contact: '',
        password: '',
        confirmPassword: ''
      }
    }
  },

  methods: {
    signIn(){
      this.$api.post('/login', this.user)
        .then(response => {
          console.log(response.data)
        })
        .catch(err => {
          console.log(err)
        })
    },

    signUp(){
      this.$api.post('/signup', this.newUser)
        .then(response => {
          if (response.data.error){
            this.showMessage('error', response.data.error)
          }
          if (response.data.userId){
            this.showMessage('success', 'Cadastro Realizado com Successo')
            this.register = false
          }
          console.log(response.data)
        })
        .catch(err => {
           this.showMessage('error', err)
        })
    },

    showMessage(type, text){
      this.snackVisible = true
      this.snackType = type
      this.snackText = text

      setTimeout(() => {
        this.snackVisible = false
        this.snackType = ''
        this.snackText = ''
      }, 5000)
    }
  }
}
</script>

