<template>
    <div>
        <v-snackbar 
            top 
            :color="type"
            v-model="visible"
        >
            {{ text }}
            <v-btn
                color="white"
                text
                @click="visible = false"
            >
                Close
            </v-btn>
        </v-snackbar>
    </div>
</template>

<script>
export default {
    props: {
        visible: {
            type: Boolean,
            default: false
        },
        text: {
            type: String,
            default: ''
        },
        type: {
            type: String,
            default: 'success'
        }
    }
}
</script>

