import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from './views/Home/Home.vue'
import Login from './views/Login/Login'
import Precos from './views/Precos/Precos'
import VideoAulas from './views/VideoAulas/VideoAulas'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'home',
        component: Home
    },
    {
        path: '/login',
        name: 'login',
        component: Login
    },
    {
        path: '/precos',
        name: 'precos',
        component: Precos
    },
    {
        path: '/video-aulas',
        name: 'video-aulas',
        component: VideoAulas
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router