<template>
  <v-app-bar
      app
      dark
      elevate-on-scroll
    >
      <div class="d-flex align-center">
        <v-img
          alt="Vuetify Logo"
          class="shrink ml-5"
          contain
          src="@/assets/logo.png"
          transition="scale-transition"
          width="120"
        />

      </div>

      <v-spacer></v-spacer>

      <v-menu
        class="hidden-md-and-up"
        bottom
        left
      >
        <template v-slot:activator="{ on }">
          <v-btn
            class="hidden-md-and-up"
            icon
            color="white"
            v-on="on"
          >
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>

        <v-list>
          <v-list-item
            v-for="(link, i) in links"
            :key="i"
          >
            <v-btn
              text 
              width="100"
              :to="link.url"
            >
              {{ link.label }}
            </v-btn>
            <!-- <v-list-item-title>{{ link.label }}</v-list-item-title> -->
          </v-list-item>
        </v-list>
      </v-menu>


      <v-btn
        text
        rounded
        class="mr-4 hidden-sm-and-down"
        width="100px"
        to="/"
        label="Home"
      >
        Home
      </v-btn>

      <!-- <v-btn
        text
        rounded
        class="mr-4 hidden-sm-and-down"
        width="100px"
        @click="smoothScroll"
        label="Contato"
      >
        Contato
      </v-btn> -->

      <v-btn
        text
        rounded
        class="mr-4 hidden-sm-and-down"
        width="100px"
        to="/precos"
        label="Preços"
      >
        Planos
      </v-btn>

      <v-btn
        text
        rounded
        class="mr-4 hidden-sm-and-down"
        width="100px"
        to="/video-aulas"
        label="Aprenda"
      >
        Aprenda
      </v-btn>

    </v-app-bar>
</template>

<script>
export default {
  data(){
    return {
      links: [
        { label: 'Home', url: '/' },
        { label: 'Planos', url: '/precos' },
        { label: 'Aprenda', url: '/video-aulas' }
      ]
    }
  },

  methods: {
    smoothScroll() {
        const target = document.getElementById('contato')
        var scrollContainer = target;
        do { //find scroll container
            scrollContainer = scrollContainer.parentNode;
            if (!scrollContainer) return;
            scrollContainer.scrollTop += 1;
        } while (scrollContainer.scrollTop == 0);
        
        var targetY = 0;
        do { //find the top of target relatively to the container
            if (target == scrollContainer) break;
            targetY += target.offsetTop;
        } while (target == target.offsetParent);
        
        function scroll(c, a, b, i) {
            i++; if (i > 30) return;
            c.scrollTop = a + (b - a) / 30 * i;
            setTimeout(() => { scroll(c, a, b, i); }, 20);
        }
        // start scrolling
        scroll(scrollContainer, scrollContainer.scrollTop, targetY, 0);
    }
  }
}
</script>

